
.Card {
    background-color: rgb(255, 255, 255, 0.03);

    border-radius: 5px;

    box-shadow: 0 8px 10px 0px rgba(0, 0, 0, 0.2);

    box-sizing: border-box;
    
    display: flex;
    flex-direction: column;
}

.CardImage {
    max-height: 12em;
    object-fit: cover;
    padding-bottom: 10px;
}